<template>
  <div
    class="
      w-full
      h-screen
      flex
      items-center
      md:justify-center
      md:pl-20
      justify-center
    "
    style="background-color: #eef0fc"
  >
    <div
      class="
        bg-white
        w-80
        h-132
        rounded-3xl
        shadow-lg
        flex flex-col
        justify-evenly
        p-4
        z-40
        overflow-hidden
      "
    >
      <div class="w-full justify-center items-center flex">
        <img
          class="w-32"
          src="../assets/lineal_animated.svg"
          srcset="../assets/lineal_animated.svg"
          alt=""
        />
      </div>

      <form class="w-full">
        <div class="flex flex-col items-center relative">
          <input
            type="text"
            placeholder="Usuario"
            class="
              focus:outline-none
              focus:ring
              p-2
              w-full
              rounded-md
              mb-2
              ring-bluegray-300
            "
            style="background-color: #f4f5f7"
            v-model="datos.usuario"
          />
          <span
            v-if="datos.input1"
            class="w-full text-left text-xs text-red-700 absolute top-10"
          >
            *Campo requerido
          </span>
        </div>

        <div class="flex flex-col items-center relative my-3">
          <input
            :type="datos.typeInput"
            placeholder="Contraseña"
            class="
              focus:outline-none
              focus:ring
              p-2
              w-full
              rounded-md
              mb-2
              ring-bluegray-300
            "
            autocomplete="off"
            style="background-color: #f4f5f7"
            v-model="datos.contrasena"
          />
          <span
            v-if="datos.input2"
            class="w-full text-left text-xs text-red-700 absolute top-10"
          >
            *Campo requerido
          </span>
        </div>

        <button
          class="
            focus:outline-none
            focus:ring
            bg-gray-600
            text-gray-50
            p-2
            w-full
            rounded-md
            mb-2
            cursor-pointer
            ring-lime-300
          "
          @click="iniciarSesion"
        >
          Entrar
        </button>

        <div
          class="text-xs w-full text-center text-gray-400 hover:text-blue-300"
        ></div>
      </form>
    </div>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import { onMounted } from "@vue/runtime-core";
import { useStore } from "vuex";
import router from "@/router";

export default {
  setup() {
    const store = useStore();
    const Swal = require("sweetalert2");

    onMounted(() => {
      localStorage.setItem("usuario", "");
      localStorage.setItem("idDestino", "");
    });

    const datos = ref({
      usuario: "",
      contrasena: "",
      typeInput: "password",
      input1: false,
      input2: false,
    });

    const iniciarSesion = async () => {
      datos.value.input1 = false;
      datos.value.input2 = false;

      // USUARIO VACIO
      if (!datos.value.usuario.length) {
        datos.value.input1 = true;
        return;
      }

      // USUARIO CONTRASEÑA
      if (!datos.value.contrasena.length) {
        datos.value.input2 = true;
        return;
      }

      const response = await store.dispatch("sesion", {
        usuario: datos.value.usuario,
        contrasena: datos.value.contrasena,
        action: "sesion",
      });

      if (!response.response) {
        Swal.fire({
          title: "Error de Sesión",
          html: `Datos incorrectos!`,
          icon: "error",
          background: "#2c3e50",
          customClass: {
            popup: "colored-toast",
            backdrop: "swal2-backdrop-show",
            icon: "swal2-icon-show",
          },
          iconColor: "#F27878",
        });
        return;
      }

      if (response.response) {
        if (response.data.data.idDestino) {
          localStorage.setItem("idDestino", response.data.data.idDestino);
          localStorage.setItem("usuario", response.data.data.idUsuario);
          router.push("/");
        }
      }
    };

    return { datos, iniciarSesion };
  },
};
</script>

<style scoped>
.h-132 {
  height: 33rem;
}
</style>